import { YELP_REQUESTED, YELP_SUCCEEDED, YELP_CHANGE_ATTENDING } from '../constants/yelp';

export default {
    yelpRequested: function (state = {isFetching: false}, action) {
        if (action.type === YELP_REQUESTED) {
            return {
                ...state,
                isFetching: action.isFetching,
                location: action.location
            }
        } else {
            return state;
        }
    },
    yelpSucceeded: function (state = {locations: null}, action) {
        if (action.type === YELP_SUCCEEDED) {
            return {
                ...state,
                locations: action.locations
            }
        } else if (action.type === YELP_CHANGE_ATTENDING) {
            return {
                ...state,
                locations: state.locations.map(location => {
                    if (location.id === action.name) {
                        location.numAttending = action.num;
                        return location;
                    } else {
                        return location;
                    }
                })
            }
        } else {
            return state;
        }
    }
}


