import React from 'react';
import { fetchPlaces } from '../actions/yelp'
import { connect } from 'react-redux';

const Search = ({ dispatch }) => {
    let input;

    const pathname = window.location.pathname.match(/^\/([a-z]*)/i)[1];
    const html = (
        <div className="container">
            <div className="row" style={{paddingTop: "10px"}}>
                <div className="col-md">
                    <form onSubmit={submit}>
                        <div className="input-group mb-3">
                            <input ref={elem => input = elem} type="text" defaultValue={pathname} className="form-control" placeholder="Where will you be tonight?" />
                            <div className="input-group-append">
                                <button className="btn btn-outline-secondary" type="submit">Search</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );

    function submit(e, path) {
        console.log(input);
        if(e) e.preventDefault();
        const text = path || input.value.trim();
        if (text){
            console.log(input);
            dispatch(fetchPlaces(text));
        } 
    }
    if (pathname)
        submit(null, pathname);
    return html;
}

export default connect()(Search)


