import { YELP_REQUESTED, YELP_SUCCEEDED, YELP_FAILED, YELP_CHANGE_ATTENDING } from '../constants/yelp';

/**
 * (Internal)
 * Action for setting initiating request
 * @param {string} location 
 */
const requested = (location, isFetching) => ({
    type: YELP_REQUESTED,
    location,
    isFetching
});

/**
 * (Internal)
 * The Yelp API request succeeded 
 * @param {Object[]} locations Array of locations
 */
const recieved = locations => ({
    type: YELP_SUCCEEDED,
    locations
});

/**
 * Action to begin request from API
 * @param {string} location Location requesting from
 */
export const fetchPlaces = location => dispatch => {
    dispatch(requested(location, true));
    fetch('/api/places/'+location, { credentials: 'include' })
    .then(res => {
        console.log(res.headers.get('set-cookie'));
        return res.json();
    })
    .then(res => {
        dispatch(requested(location, false));
        dispatch(recieved(res));
    })
    .catch(err => {
        console.log(err);
        dispatch(requested(null, false))
    })
};

/**
 * Change the amount of people going to a certain location
 * @param {string} name Identification of location to change
 * @param {number} num The new number of people attending this location
 */
export const changeNumAttending = (name, num) => ({
    type: YELP_CHANGE_ATTENDING,
    name,
    num
});


