import {ATTENDING_REQUESTED, ATTENDING_SUCCEEDED} from '../constants/api';
import { changeNumAttending } from './yelp';

export default function (id) {
    return function (dispatch) {
        return fetch('/api/attending/' + id, { credentials: 'include' })
            .then(res => res.json())
            .then(res => {
                if (res.notLogged) {
                    window.location = "http://localhost:5000/api/login";
                } else {
                    dispatch(changeNumAttending(id, res.people.length));
                }
            })
            .catch(err => {});
    }
}


