import React from 'react';
import Location from './location';

export default ({ isFetching, locations }) => {
    console.log('sup brah', isFetching, locations)
    if (!isFetching && locations) {
        return (
            <div className="container">
                <div className="row">
                    {locations.map(loc => <Location location={loc}></Location>)}
                </div>
            </div>
        );
    } else {
        return null;
    }
};


